<template>
  <div>
    <canvas ref="canvas" :width="width" :height="height" class="canvas"></canvas>
  </div>
</template>
<script>
export default {
  name: 'CanvasPoster',
  components: {},
  props: {
    painting: {
      type: Object,
      value: {}
    }
  },
  computed: {
  },
  watch: {
    painting: {
      handler: 'handlePaint',
      deep: true,
      immediate: true
    },
  },
  data() {
    return {
      showCanvas: false,
      width: 100,
      height: 100,
      imageList: [],
      isPainting: false,
      canvas: null,
      ctx: null
    }
  },

  mounted() {
    this.$nextTick(() => {
      this.canvas = this.$refs.canvas // canvas
      this.ctx = this.canvas.getContext("2d")//2D
    })
  },
  methods: {
    // 
    handlePaint(newVal, oldVal) {
      const { width, height, views } = this.painting
      this.width = width
      this.height = height
      const inter = setInterval(() => {
        if (this.ctx) {
          clearInterval(inter)
          // 
          this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height)
          this.ctx.save()
          this.getImages(views)
        }
      }, 100)
    },
    //
    loadImage(url) {
      return new Promise((resolve, reject) => {
        const img = new Image()
        img.setAttribute('crossorigin', 'anonymous');
        img.onload = () => resolve(img)
        img.onerror = () => reject(` ${url}`)
        img.src = url
      })
    },
    // 
    getImages(views) {
      const imageList = []
      for (let i = 0; i < views.length; i++) {
        if (views[i].type === 'image') {
          imageList.push(this.loadImage(views[i].url))
        }
      }
      Promise.all(imageList).then(res => {
        this.imageList = res
        this.startPainting()
      })
    },
    startPainting() {
      const { views } = this.painting
      for (let i = 0, imageIndex = 0; i < views.length; i++) {
        if (views[i].type === 'image') {
          this.drawImage({
            ...views[i],
            img: this.imageList[imageIndex]
          })
          imageIndex++
        } else if (views[i].type === 'text') {
          this.drawText(views[i])
        } else if (views[i].type === 'rect') {
          this.drawRect(views[i])
        }
      }
      var imageBase64 = this.canvas.toDataURL("image/png")
      this.$emit('success', imageBase64)

    },
    drawImage(params) {
      this.ctx.save()
      const { img, top = 0, left = 0, width = 0, height = 0, borderRadius = 0, deg = 0 } = params
      // if (borderRadius) {
      //   this.ctx.beginPath()
      //   this.ctx.arc(left + borderRadius, top + borderRadius, borderRadius, 0, 2 * Math.PI)
      //   this.ctx.clip()
      //   this.ctx.drawImage(img, left, top, width, height)
      // } else {
      if (deg !== 0) {
        this.ctx.translate(left + width / 2, top + height / 2)
        this.ctx.rotate(deg * Math.PI / 180)
        this.ctx.drawImage(img, -width / 2, -height / 2, width, height)
      } else {
        this.ctx.drawImage(img, left, top, width, height)
      }
      //   }
      this.ctx.restore()
    },
    // 
    drawText(params) {
      this.ctx.save()
      const {
        MaxLineNumber = 2,
        breakWord = false,
        color = 'black',
        content = '',
        fontSize = 16,
        top = 0,
        left = 0,
        lineHeight = 20,
        textAlign = 'left',
        width,
        bolder = false,
        textDecoration = 'none'
      } = params

      this.ctx.beginPath()
      this.ctx.textBaseline = 'top'
      this.ctx.textAlign = textAlign
      this.ctx.fillStyle = color
      //  this.ctx.font = "normal 36px Arial";
      this.ctx.font = `normal ${fontSize}px`;
      if (!breakWord) {
        this.ctx.fillText(content, left, top)
        this.drawTextLine(left, top, textDecoration, color, fontSize, content)
      } else {
        let fillText = ''
        let fillTop = top
        let lineNum = 1
        for (let i = 0; i < content.length; i++) {
          fillText += [content[i]]
          if (this.ctx.measureText(fillText).width > width) {
            if (lineNum === MaxLineNumber) {
              if (i !== content.length) {
                fillText = fillText.substring(0, fillText.length - 1) + '...'
                this.ctx.fillText(fillText, left, fillTop)
                this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText)
                fillText = ''
                break
              }
            }
            this.ctx.fillText(fillText, left, fillTop)
            this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText)
            fillText = ''
            fillTop += lineHeight
            lineNum++
          }
        }
        this.ctx.fillText(fillText, left, fillTop)
        this.drawTextLine(left, fillTop, textDecoration, color, fontSize, fillText)
      }
      this.ctx.restore()
      if (bolder) {
        this.drawText({
          ...params,
          left: left + 0.3,
          top: top + 0.3,
          bolder: false,
          textDecoration: 'none'
        })
      }
    },
    // 
    drawTextLine(left, top, textDecoration, color, fontSize, content) {
      if (textDecoration === 'underline') {
        this.drawRect({
          background: color,
          top: top + fontSize * 1.2,
          left: left - 1,
          width: this.ctx.measureText(content).width + 3,
          height: 1
        })
      } else if (textDecoration === 'line-through') {
        this.drawRect({
          background: color,
          top: top + fontSize * 0.4,
          left: left - 1,
          width: this.ctx.measureText(content).width + 3,
          height: 1
        })
      }
    },
    // 
    drawRect(params) {
      this.ctx.save()
      const { background, top = 0, left = 0, width = 0, height = 0, radius = 0 } = params
      this.ctx.fillStyle = background
      if (radius) {
        //  
        this.ctx.beginPath()
        //  
        this.ctx.arc(left + radius, top + radius, radius, Math.PI, Math.PI * 1.5)
        // border-top
        this.ctx.moveTo(left + radius, top)
        this.ctx.lineTo(left + width - radius, top)
        this.ctx.lineTo(left + width, top + radius)
        // 
        this.ctx.arc(left + width - radius, top + radius, radius, Math.PI * 1.5, Math.PI * 2)
        // border-right 
        this.ctx.lineTo(left + width, top + height - radius)
        this.ctx.lineTo(left + width - radius, top + height)
        //  
        this.ctx.arc(left + width - radius, top + height - radius, radius, 0, Math.PI * 0.5)
        // border-bottom
        this.ctx.lineTo(left + radius, top + height); this.ctx.lineTo(left, top + height - radius)
        //  
        this.ctx.arc(left + radius, top + height - radius, radius, Math.PI * 0.5, Math.PI)
        // border-left
        this.ctx.lineTo(left, top + radius);
        this.ctx.lineTo(left + radius, top)
        //  fill  stroke
        this.ctx.fill()
        this.ctx.closePath()
      } else {
        this.ctx.fillRect(left, top, width, height)
      }
      this.ctx.restore()
    },
    saveImageToLocal() {
      const { width, height } = this.data
      wx.canvasToTempFilePath({
        x: 0,
        y: 0,
        width,
        height,
        canvasId: 'canvasdrawer',
        complete: res => {
          if (res.errMsg === 'canvasToTempFilePath:ok') {
            this.setData({
              showCanvas: false,
              isPainting: false,
              tempFileList: []
            })
            this.triggerEvent('getImage', { tempFilePath: res.tempFilePath, errMsg: 'canvasdrawer:ok' })
          } else {
            this.triggerEvent('getImage', { errMsg: 'canvasdrawer:fail' })
          }
        }
      }, this)
    }
  }
}

</script>
<style  scoped>
.canvas {
  position: fixed;
  top: 2000px;
}
</style>